body
  background-color: #7f5a83
  background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%)


.item
  display: flex
  color: white
  height: 210px

.link
  background: #8d2828
  color: white
  display: flex
  width: 200px
  height: 40px
  justify-content: center
  align-items: center
  text-decoration: none
  padding-bottom: 3px
  margin-left: 10px
  margin-top: 20px
  transition: 0.3s
  &:hover
    color: white
    transition: 0.3s
    background: goldenrod
  
.position
  width: 100%
  position: relative

.titleName
  position: absolute
  top: 50px
  right: 200px
  font-size: 80px
  text-transform: uppercase
  color: burlywood

.description
  padding-right: 20px
  text-align: justify
  font-size: 20px
  margin-top: 5px
  margin-left: 10px
  text-shadow: gold 5px 5px 10px
